.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
.j-input {
  position: relative;
  width: 100%;
  max-width: 14em;
  font-weight: 400;
  font-style: normal;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  color: rgba(0, 0, 0, 0.87);
  font-size: 1em;
}
.j-input.is-disabled {
  opacity: .45;
}
.j-input > .fa {
  top: 50%;
  margin-top: -0.5em;
  color: #999;
}
.j-input > .fa.left {
  left: 0;
}
.j-input > .fa.right {
  right: 0;
  left: unset;
}
.j-input > input {
  width: 100%;
  margin: 0;
  outline: 0;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  text-align: left;
  line-height: 14px;
  font-family: Lato, 'Helvetica Neue', Arial, Helvetica, sans-serif;
  padding: 8px 12px;
  background: #fff;
  border: 1px solid rgba(34, 36, 38, 0.15);
  color: rgba(0, 0, 0, 0.87);
  border-radius: 4px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: border-color 0.1s ease, -webkit-box-shadow 0.1s ease;
  transition: border-color 0.1s ease, -webkit-box-shadow 0.1s ease;
  transition: box-shadow 0.1s ease, border-color 0.1s ease;
  transition: box-shadow 0.1s ease, border-color 0.1s ease, -webkit-box-shadow 0.1s ease;
  -webkit-box-shadow: none;
          box-shadow: none;
}
.is-error .j-input > input {
  background-color: #fff6f6;
  border-color: #e0b4b4;
  color: #9f3a38;
  -webkit-box-shadow: none;
          box-shadow: none;
}
.has-left-icon.j-input > input {
  padding-left: 3em;
}
.has-right-icon.j-input > input {
  padding-right: 3em;
}
.j-input > input:focus {
  border-color: #85b7d9;
  background: #fff;
  color: rgba(0, 0, 0, 0.8);
  -webkit-box-shadow: none;
          box-shadow: none;
