.j-form-item {
  margin: 0 auto 22px;
  line-height: 32px;
}
.j-form-item .fade-enter-active,
.j-form-item .fade-leave-active {
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
.j-form-item .fade-enter,
.j-form-item .fade-leave-to {
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
}
.j-form-item:before,
.j-form-item:after {
  content: "";
  display: table;
  clear: both;
}
.j-form-item.label-left .j-form-item__label {
  text-align: left;
