.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
.j-dropdown {
  font-size: 14px;
  padding: 8px 1em;
  position: relative;
  max-width: 14em;
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
  cursor: pointer;
  outline: 0;
  color: rgba(0, 0, 0, 0.87);
  border: 1px solid rgba(34, 36, 38, 0.15);
  background-color: #fff;
  border-radius: 4px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 10;
}
.is-error .j-dropdown {
  background-color: #fff6f6;
  border-color: #e0b4b4;
  color: #9f3a38;
}
.j-dropdown.is-active {
  border-color: rgba(34, 36, 38, 0.35);
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.j-dropdown:hover {
  border-color: rgba(34, 36, 38, 0.35);
}
.j-dropdown:focus {
  border-color: #96c8da;
}
.j-dropdown > .fa {
  position: absolute;
  top: 50%;
  right: 10px;
  margin-top: -0.5em;
}
.j-dropdown .text {
  line-height: 1;
  -webkit-transition: none;
  transition: none;
}
.j-dropdown .text.default {
  color: rgba(191, 191, 191, 0.87);
}
.j-dropdown .menu {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  max-height: 14em;
  font-size: 1em;
  margin: 0 -1px;
  outline: 0;
  border-radius: 0 0 4px 4px;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-transition: opacity .1s ease;
  transition: opacity .1s ease;
  text-align: left;
  visibility: hidden;
  border: 1px solid rgba(34, 36, 38, 0.15);
  margin-top: -1px;
  background-color: #fff;
  border-top-width: 0;
}
.is-active.j-dropdown .menu {
  visibility: visible;
  border-color: #96c8da;
  -webkit-box-shadow: 0 2px 3px 0 rgba(34, 36, 38, 0.15);
          box-shadow: 0 2px 3px 0 rgba(34, 36, 38, 0.15);
  z-index: 11;
}
.j-dropdown .menu .item {
  color: rgba(0, 0, 0, 0.87);
  border-top: 1px solid #fafafa;
  padding: .8em 1.2em;
  line-height: 1;
  white-space: normal;
  word-wrap: normal;
}
.j-dropdown .menu .item:hover {
  background: rgba(0, 0, 0, 0.05);
  color: rgba(0, 0, 0, 0.95);
}
.j-dropdown .menu .item.selected {
  background: rgba(0, 0, 0, 0.03);
  color: rgba(0, 0, 0, 0.95);
  font-weight: bold;
}
