.j-date-picker {
  display: inline-block;
  position: relative;
}
.j-date-picker > .panel {
  z-index: 10;
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: 10px;
  padding: 0 15px 15px;
  font-size: 12px;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #dfe4ed;
  -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
.j-date-picker > .panel > .panel-header {
  margin: 12px;
  text-align: center;
  line-height: 28px;
}
.j-date-picker > .panel > .panel-header:after {
  content: '';
  display: block;
  clear: both;
}
.j-date-picker > .panel > .panel-header .f-left {
  float: left;
}
.j-date-picker > .panel > .panel-header .f-right {
  float: right;
}
.j-date-picker > .panel > .panel-header button {
  font-size: 16px;
  color: #2d2f33;
  border: 0;
  background: transparent;
  cursor: pointer;
  outline: none;
  line-height: 1;
  padding: 0 7px;
  margin-top: 6px;
}
.j-date-picker > .panel > .panel-header button:hover {
  color: #2185D0;
}
.j-date-picker > .panel > .panel-header span {
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  padding: 0 5px;
  text-align: center;
  cursor: pointer;
  color: #5a5e66;
}
.j-date-picker > .panel > .panel-body table {
  width: 100%;
}
.j-date-picker > .panel > .panel-body table th,
.j-date-picker > .panel > .panel-body table td {
  width: 24px;
  height: 24px;
  text-align: center;
  cursor: pointer;
}
.j-date-picker > .panel > .panel-body table td {
  width: 30px;
  height: 30px;
  padding: 2px 0;
  font-size: 12px;
}
.j-date-picker > .panel > .panel-body table td > div {
  margin: 0 auto;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.j-date-picker > .panel > .panel-body table td > div.pre,
.j-date-picker > .panel > .panel-body table td > div.next {
  color: #b4bccc;
}
.j-date-picker > .panel > .panel-body table td > div.today {
  color: #2185D0;
  font-weight: bold;
  border: 1px solid #2185D0;
}
.j-date-picker > .panel > .panel-body table td:hover div {
  background-color: #f0f0f0;
  font-weight: bold;
  color: inherit;
  border-color: transparent;
}

.card {
  margin-bottom: 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #fff;
  -webkit-box-shadow: 0 2px 3px 0 #e9e9e9;
          box-shadow: 0 2px 3px 0 #e9e9e9;
  border: 1px solid #e9e9e9;
  border-radius: 2px 2px 0 0;
}
.card .card-body {
  padding: 32px 10px;
}
.card .card-meta {
  padding: 18px 32px;
  position: relative;
  font-size: 16px;
  line-height: 2.4;
  border-top: 1px solid #e9e9e9;
}
.card .card-meta .meta-title {
  position: absolute;
  top: -14px;
  padding: 1px 8px;
  font-weight: bold;
  font-size: 14px;
  line-height: 2;
  background-color: #fff;
}
.card .card-meta > span {
  margin: 0 3px;
  background: #f7f7f7;
  padding: .2em .4em;
  border-radius: 3px;
  font-size: .9em;
  border: 1px solid #eee;
}
.card .card-meta .expand-icon {
  position: absolute;
  right: 16px;
  bottom: 23px;
  cursor: pointer;
  width: 16px;
  height: 16px;
  line-height: 16px;
  text-align: center;
}
.card .card-meta .expand-icon > img {
  opacity: .55;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  max-width: 100%;
  vertical-align: baseline;
  -webkit-box-shadow: none;
          box-shadow: none;
}
.card .card-meta .expand-icon > img.show {
  opacity: 1;
  pointer-events: auto;
}
.card .card-meta .expand-icon > img.hide {
  opacity: 0;
  pointer-events: none;
}
.card .card-code {
  border-top: 1px solid #e9e9e9;
}
